	.onetap-container-toggle {

		button {
			border: 2px solid transparent !important;
			background: none !important;
			padding: 0 !important;
			transition: transform 0.15s ease !important;
			width: auto !important;
			height: auto !important;

			img {
				box-sizing: border-box !important;
			}
			
			&:focus {
				border-radius: 100% !important;
				color: $light-color !important;
				transform: none !important;	
			}			
		}

		// Toggle.
		.onetap-toggle {
			z-index: 9999999999999999 !important;
			position: fixed !important;
			cursor: pointer !important;
			box-shadow: none !important;
			right: 0 !important;
			margin-right: 18px !important;
			bottom: 0 !important;
			margin-bottom: 18px !important;
			width: max-content !important;

			&.design-border1:focus {
				outline: none !important;
			}

			&:hover,
			&:focus {
				outline: none !important;
				box-shadow: none !important;
				border: 2px solid transparent !important;				
			}			

			img {
				height: 60px !important;
				width: 60px !important;
				background: $primary-color !important;
				border-radius: 50% !important;
				display: block !important;
				transition-duration: .3s !important;
				transition-property: transform !important;

				&:hover {
					transform: scale(1.1) !important;
				}				
			}

			img.design-size1 {
				padding: 10px !important;
				width: 50px !important;
				height: 50px !important;
			}

			img.design-size2 {
				padding: 15px !important;
				width: 65px !important;
				height: 65px !important;
			}

			img.design-size3 {
				padding: 17.5px !important;
				width: 80px !important;
				height: 80px !important;
			}

			img.design-border1 {
				border: solid 2px #fff;
				box-shadow: 0 0 0 4px $primary-color;
			}

			svg {
				width: 55px !important;
				fill: #111 !important;
				border-radius: 100% !important;

				&:hover {
					box-shadow: 0 0 0 5px #0000000d;
					border-radius: 100px;
				}
			}

		}
	}

	nav.onetap-accessibility.onetap-plugin-onetap {
		box-sizing: border-box !important;
		display:  block !important;
		padding: 0 !important;
		margin: 0 !important;
		text-align: left !important;
		line-height: 1.7 !important;
		letter-spacing: 0 !important;
		font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, Cantarell, "Helvetica Neue", Oxygen, "Fira Sans", "Droid Sans", sans-serif !important;
		font-size: 16px !important;
		max-width: $width !important;
