		nav.onetap-accessibility.onetap-plugin-onetap {
			width: 100% !important;

			.onetap-container {

				.onetap-accessibility-settings {

					header.onetap-header-top {

						.onetap-languages {
							left: 10px !important;
						}

						.onetap-site-container {

							.onetap-site-info {

								.onetap-title {

									.onetap-heading {
										font-size: 1.5em !important;
									}
								}
							}
						}
					}

					// Multi functional feature.
					div.onetap-multi-functional-feature {
						padding: 10px !important;
						padding-bottom: 0 !important;

						.onetap-box-functions {

							.onetap-functional-feature {

								.onetap-left {

									.onetap-icon {
										display: none !important;
									}
								}
							}
						}
					}

					// Features.
					div.onetap-features-container {
						padding: 12.5px 10px 0 10px !important;
						margin-bottom: 0 !important;

						&.onetap-feature-content-modules {
							margin-bottom: 0 !important;
						}

						.onetap-features {
							padding: 12px !important;

							.onetap-box-features {

								.onetap-box-step-controls {

									.onetap-new-level {

										.onetap-btn {
											width: 23px !important;
											height: 23px !important;
										}
									}
								}

								.onetap-box-step-controls {
									width: 100% !important;
									padding: 5px !important;
								}

								.onetap-box-step-controls:first-child + button.onetap-box-feature {
									width: 100% !important;
								}								

								button.onetap-box-feature {
									width: calc(50% - 5px) !important;
									padding: 5px !important;

									.onetap-title {

										p.onetap-option-levels {
											justify-content: center !important;

											span.onetap-level {
												width: 20px !important;
											}

											.onetap-heading {
												margin: 0 0 10px 0 !important;
											}
										}
									}
								}

							}
						}
					}

					// Reset settings.
